<template>
  <v-container>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step
          :complete="e1 > 1"
          step="1"
          @click="e1 = 1"
        >Общая информация</v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step
          :complete="e1 > 2"
          step="2"
          @click="e1 = 2"
        >Реквизиты</v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step
          step="3"
          @click="e1 = 3"
        >Условия</v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step
          step="4"
          @click="e1 = 4"
        >Блоки</v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card
            class="mb-12"
          >
            <v-row>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Наименование</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="name"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Город</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="city"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Контактное лицо</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="clientName"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Телефон</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="phone"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">E-mail</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="email"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card>

          <v-btn
            color="primary"
            to="/sales-channels/list"
          >
            Сохранить
          </v-btn>

          <v-btn text>Выход</v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <v-card
            class="mb-12"
            color="grey lighten-1"
            height="200px"
          ></v-card>

          <v-btn
            color="primary"
            to="/sales-channels/list"
          >
            Сохранить
          </v-btn>

          <v-btn text>Выход</v-btn>
        </v-stepper-content>

        <v-stepper-content step="3">
          <v-card
            class="mb-12"
          >
            <v-row>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Комиссия</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="comission"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Сроки оплаты</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="days"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Просмотр календаря</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="calendar"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Форма бронирования</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="bookingForm"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Разрешить статусы</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="statusPerm"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Лимиты на продажу</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="saleLimit"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card>

          <v-btn
            color="primary"
            to="/sales-channels/list"
          >
            Сохранить
          </v-btn>

          <v-btn text>Выход</v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </v-container>
</template>

<script>
  export default {
    name: 'CreateSource',
    data () {
      return {
        e1: 1,
        name: null,
        clientName: null,
        email: null,
        phone: null,
        city: null,
        bookingForm: null,
        statusPerm: null,
        saleLimit: null,
        comission: null,
        days: null,
        calendar: null,
      }
    },
  }
</script>

<style scoped>
  .title {
    font-size: 18px !important;
    font-weight: 500 !important;
    color: orange !important;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
</style>
